.frame {
  width: 93vw;
  height: 89vh;

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  overflow: overlay;
}

.frame iframe {
    width: 100%;
    height: 100%;
  
    border: none;
    overflow: overlay;
}